function FlowCanvasInner() {
  const reactFlowWrapper = useRef<HTMLDivElement>(null)
  
  // Connect to Zustand Store
  const { nodes, edges, onNodesChange, onEdgesChange, addNode, onConnect } = useStore() as any;
  const [reactFlowInstance, setReactFlowInstance] = useState<ReactFlowInstance | null>(null)

  const onDragOver = useCallback((event: React.DragEvent) => {
    event.preventDefault()
    event.dataTransfer.dropEffect = 'move'
  }, [])

  const onDrop = useCallback(
    (event: React.DragEvent) => {
      event.preventDefault()
      const brickData = event.dataTransfer.getData('application/reactflow')
      if (!brickData || !reactFlowInstance) return

      const brick: BrickType = JSON.parse(brickData)
      const position = reactFlowInstance.screenToFlowPosition({
        x: event.clientX,
        y: event.clientY,
      })

      // Create unique ID and deep copy files so they are editable
      const id = `${brick.id}_${Math.random().toString(36).substr(2, 9)}`
      
      const newNode: Node = {
        id,
        type: 'custom',
        position,
        data: { 
          label: brick.label,
          brickId: brick.id,
          // This maps the brick files into the live state
          files: brick.files.map(f => ({ ...f })) 
        },
      }

      addNode(newNode)
    },
    [reactFlowInstance, addNode]
  )

  return (
    <div ref={reactFlowWrapper} className="flex-1 h-full w-full relative">
      <ReactFlow
        nodes={nodes}
        edges={edges}
        onNodesChange={onNodesChange}
        onEdgesChange={onEdgesChange}
        onConnect={onConnect}
        onInit={setReactFlowInstance}
        onDrop={onDrop}
        onDragOver={onDragOver}
        nodeTypes={nodeTypes}
        fitView
      >
        <Background gap={20} size={1} />
        <Controls className="fill-foreground" />
        <MiniMap />
      </ReactFlow>
    </div>
  )
}